#myBanner {
    height: 60%;
    position: relative;
    img {
        width: 100%;
        height: 100%;
    }
    .systemName {
        position: absolute;
        top: 100px;
        width: 100%;
        text-align: center;
        span {
            font: 58px/68px "微软雅黑";
        }
        p {
            font: 26px/30px "微软雅黑";
            color: #828f98;
        }
    }
}

#swiperBox {
    width: 70%;
    height: 40%;
    margin: 30px auto 0;
    .swiper-slide {
        text-align: center;
        a {
            display: inline-block;
            width: 60%;
            padding: 10px 15px;
            cursor: pointer;
            .imgBox {}
            span {
                display: block;
                margin: 5px;
                letter-spacing: 2px;
                font-weight: bold;
                padding: 5px;
                border-radius: 4px;
                color: #fff;
                background: #1dafd4;
            }
        }
        a:hover {
            opacity: .7;
            filter: alpha(opacity=70);
        }
        .subList {
            position: absolute;
            margin-left: 5px;
            padding: 0 5%;
            width: 90%;
            max-height: 70%;
            top: 10px;
            background: #fff;
            border-radius: 5px;
            opacity: 0;
            filter: alpha(opacity=0);
            transition: all .8s ease-in-out;
            a {
                width: 100%;
                color: #000;
                padding: 5px 0;
            }
        }
        .show {
            opacity: .9;
            filter: alpha(opacity=90);
            transform: rotate(360deg);
            -webkit-transform: rotate(360deg);
            -moz-transform: rotate(360deg);
            -o-transform: rotate(360deg);
            -ms-transform: rotate(360deg);
        }
    }
    .swiper-button-prev {
        left: 0;
    }
    .swiper-button-right {
        right: 0;
    }
}

.ng-scope {
    height: 100%;
}

.banner .tab-box1 .box {
    width: 160px !important;
}

.banner .tab-box1 li:nth-of-type(2) {
    left: 160px !important;
}

.banner .tab-box1 li:nth-of-type(4) {
    left: 320px !important;
}

.banner .log {
    width: 200px;
    span {
        width: 130px;
    }
}

.logOut {
    position: absolute;
    right: 0;
    top: 5px;
    z-index: 100;
    cursor: pointer;
    .userImg {
        width: 35px;
        height: 35px;
        margin-right: 18px;
    }
    .userName {
        margin-right: 6px;
        color: #fff;
    }
    .icon-chevron-down {
        color: #fff;
        margin-right: 10px;
    }
    .message {
        padding-left: 0;
        margin-top: 10px;
        list-style: none;
        li {
            line-height: 20px;
            text-align: center;
        }
    }
}


